.tu-radio {
  color: #9baacf;
  font-weight: 500;
  line-height: 1;
  position: relative;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  white-space: nowrap;
  outline: none;
  font-size: 14px;
  margin-right: 30px;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none; }
  .tu-radio.is-bordered {
    /**边框阴影会造成视觉上位置偏移，故阴影处增加1px，发光处减少1px*/
    padding: calc(9px + 1px) calc(9px + 6px) calc(9px - 1px) 9px;
    color: #9baacf;
    background: transparent;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    box-sizing: border-box;
    border-radius: 4px; }
    .tu-radio.is-bordered .tu-radio__inner {
      box-shadow: none; }
    .tu-radio.is-bordered .tu-radio__label {
      padding-left: 0; }
    .tu-radio.is-bordered.is-checked {
      box-shadow: inset 2px 2px 5px #c8d0e7, inset -2px -2px 5px #ffffff; }
      .tu-radio.is-bordered.is-checked .tu-radio__inner {
        box-shadow: none; }
  .tu-radio.is-disabled {
    cursor: not-allowed;
    position: relative; }
    .tu-radio.is-disabled .tu-radio__inner::after, .tu-radio.is-disabled .tu-radio__inner:hover::after, .tu-radio.is-disabled .tu-radio__inner:focus::after, .tu-radio.is-disabled .tu-radio__inner:active::after {
      background: #bec8e4; }
    .tu-radio.is-disabled .tu-radio__label {
      color: #bec8e4; }
    .tu-radio.is-disabled.is-checked .tu-radio__inner::after {
      background: #a79efd; }
  .tu-radio.is-checked .tu-radio__inner {
    box-shadow: inset 2px 2px 4px #c8d0e7, inset -2px -2px 5px #ffffff; }
    .tu-radio.is-checked .tu-radio__inner::after {
      background: #6d5dfc; }
  .tu-radio.is-focus .tu-radio__inner::after {
    background: #6d5dfc;
    box-shadow: 0 0 3px #8abdff; }
  .tu-radio--mini.is-bordered {
    padding: calc(3px) calc(3px + 6px) calc(3px) 3px; }
    .tu-radio--mini.is-bordered .tu-radio__inner {
      width: 14px;
      height: 14px; }
    .tu-radio--mini.is-bordered .tu-radio__label {
      font-size: 10px; }
  .tu-radio--small.is-bordered {
    padding: calc(6px) calc(6px + 6px) calc(6px) 6px; }
    .tu-radio--small.is-bordered .tu-radio__inner {
