  .tu-radio--large.is-bordered {
    padding: calc(11px) calc(15px + 6px) calc(11px) 15px; }
    .tu-radio--large.is-bordered .tu-radio__inner {
      width: 22px;
      height: 22px; }
    .tu-radio--large.is-bordered .tu-radio__label {
      font-size: 14px; }
  .tu-radio__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle; }
  .tu-radio__inner {
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    width: 20px;
    height: 20px;
    box-shadow: 3px 3px 6px #c8d0e7, -2px -2px 5px #ffffff;
    position: relative;
    cursor: pointer;
    border-radius: 50%; }
    .tu-radio__inner:hover::after {
